import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Device, ManageMyDevicesService, PatchDeviceRequest } from '@backbase/data-ang/device';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class UserDeviceInformationService {
  constructor(private readonly deviceManagementService: ManageMyDevicesService) {}

  getDevices(): Observable<Array<Device>> {
    return this.deviceManagementService.getMyDevices().pipe(map(response => response.devices));
  }

  updateDevice(deviceId: string, patchDeviceRequest: PatchDeviceRequest): Observable<HttpResponse<any>> {
    return this.deviceManagementService.patchMyDeviceByDeviceId({
      deviceId,
      patchDeviceRequest,
    });
  }

  removeDevice(deviceId: string): Observable<HttpResponse<any>> {
    return this.deviceManagementService.deleteMyDeviceByDeviceId({ deviceId });
  }
}
