import { Component, Input, Output, EventEmitter, Directive, ChangeDetectionStrategy } from '@angular/core';
import { Device, ModifiedDevice } from '../../models/device-management-types';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-device-information-ui',
  templateUrl: './device-information.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DeviceInformationComponent {
  @Input()
  set isSaving(value: boolean) {
    this.isSavingState = value;
    if (!this.isSavingState) {
      this.toggleManageDevice();
    }
  }
  /**
   * The list of devices to display.
   */
  @Input() devices: Device[] | undefined;
  /**
   * Whether the widget is loading.
   * Defaults to false.
   */
  @Input() isLoading = false;
  /**
   * The error state of the widget.
   */
  @Input() errorState: Error | undefined;
  @Output() save = new EventEmitter<ModifiedDevice>();
  @Output() remove = new EventEmitter<Device>();
  @Output() suspend = new EventEmitter<Device>();
  @Output() restore = new EventEmitter<Device>();

  hostRef = this;
  private isSavingState = false;
  private managedDeviceIndex: number | undefined;

  /**
   * Sets whether the device information is being saved.
   */
  get isSaving() {
    return this.isSavingState;
  }

  toggleManageDevice(index?: number) {
    this.managedDeviceIndex = index;
  }

  isManagingDevice(index?: number) {
    return index === this.managedDeviceIndex;
  }

  onRestore(device: Device) {
    this.restore.emit(device);
  }

  onRemove(device: Device) {
    this.remove.emit(device);
  }

  onSuspend(device: Device) {
    this.suspend.emit(device);
  }

  onSave(device: ModifiedDevice) {
    this.save.emit(device);
  }
}

@Directive({
  selector: '[bbDeviceInformationWidgetCustomizable]',
})
export class DeviceInformationWidgetCustomizableDirective extends BbTemplate<DeviceInformationComponent, undefined> {}
