import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Device, DeviceStatus } from './../../models/device-management-types';

@Component({
  selector: 'bb-device-list-item-ui',
  templateUrl: './device-list-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DeviceListItemComponent implements OnInit {
  /**
   * The device to display information for.
   */
  @Input() device: Device | undefined;
  /**
   * Whether the manage button is disabled.
   * Defaults to false.
   */
  @Input() isManageDisabled = false;

  @Output() manage = new EventEmitter<void>();
  @Output() remove = new EventEmitter<Device>();
  @Output() restore = new EventEmitter<Device>();
  @Output() suspend = new EventEmitter<Device>();

  isRestoring = false;
  isRemoving = false;
  isSuspending = false;
  icon = '';

  ngOnInit() {
    if (this.device) {
      this.icon = this.device.vendor === 'Apple' ? 'phone-iphone' : 'phone-android';
    }
  }

  isDeviceDisabled() {
    return this.device && this.device.status === DeviceStatus.DISABLED;
  }

  isDeviceRemoved() {
    return this.device && this.device.status === DeviceStatus.REMOVED;
  }

  onManage() {
    this.manage.emit();
  }

  onRestore() {
    if (this.device) {
      this.restore.emit(this.device);
      this.isRestoring = false;
    }
  }

  onRemove() {
    if (this.device) {
      this.remove.emit(this.device);
      this.isRemoving = false;
    }
  }

  onSuspend() {
    if (this.device) {
      this.suspend.emit(this.device);
      this.isSuspending = false;
    }
  }
}
