import { Injectable, Optional } from '@angular/core';
import { ItemModel } from '@backbase/foundation-ang/core';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

export interface DefaultProperties {
  dismissTimeout: number;
  isAdminWidget: boolean;
}

const defaultProperties: DefaultProperties = {
  dismissTimeout: 3,
  isAdminWidget: false,
};

@Injectable()
export class DeviceInformationPreferenceService {
  constructor(@Optional() private readonly model?: ItemModel) {}

  getTimeoutPreference(): Observable<number> {
    return this.model
      ? this.model.property<number>('dismissTimeout', defaultProperties.dismissTimeout)
      : of(defaultProperties.dismissTimeout);
  }

  getAdminPreference(): Observable<boolean> {
    return this.model
      ? this.model
          .property<string>('isAdminWidget', JSON.stringify(defaultProperties.isAdminWidget))
          .pipe(map((value: string) => !!value && value !== 'false'))
      : of(defaultProperties.isAdminWidget);
  }
}
