import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnInit,
  ChangeDetectorRef,
  Directive,
  ChangeDetectionStrategy,
} from '@angular/core';
import { Validators, FormGroup, AbstractControl, FormBuilder } from '@angular/forms';
import { Device, ModifiedDevice } from '../../models/device-management-types';
import { BbTemplate } from '@backbase/foundation-ang/core';

@Component({
  selector: 'bb-manage-device-form-ui',
  templateUrl: './manage-device-form.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ManageDeviceFormComponent implements OnInit {
  @Input()
  set device(value: Device) {
    this.managedDevice = value;
  }
  /**
   * Whether the device is being saved.
   * Defaults to false.
   */
  @Input() isSaving = false;
  @Output() save = new EventEmitter<ModifiedDevice>();
  @Output() cancel = new EventEmitter<void>();
  @Output() suspend = new EventEmitter<string>();
  @Output() remove = new EventEmitter<string>();

  form: FormGroup | undefined;
  isSubmitted = false;
  hostRef = this;
  private managedDevice: Device | undefined;

  constructor(private readonly formBuilder: FormBuilder, private readonly cd: ChangeDetectorRef) {}

  /**
   * Sets the device to be managed.
   */
  get device(): Device {
    if (this.managedDevice) {
      return this.managedDevice;
    }

    throw new Error('Device not set');
  }

  get control(): AbstractControl {
    if (this.form) {
      return this.form.controls.friendlyName;
    }
    throw new Error('Form not set');
  }

  ngOnInit() {
    this.form = this.formBuilder.group({
      friendlyName: [this.device.friendlyName || '', Validators.required],
    });
  }

  onCancel() {
    this.cancel.emit();
  }

  onSave() {
    this.isSubmitted = true;
    this.control.markAsTouched();
    this.cd.markForCheck();

    if (this.form && this.form.valid) {
      this.save.emit({ ...this.device, friendlyName: this.control.value, oldFriendlyName: this.device.friendlyName });
    }
  }
}

@Directive({
  selector: '[bbDeviceInformationFormCustomizable]',
})
export class DeviceInformationFormCustomizableDirective extends BbTemplate<ManageDeviceFormComponent, undefined> {}
