import { HttpErrorResponse } from '@angular/common/http';
import { Component, TemplateRef, ViewChild, OnInit, Input } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { switchMap, first, tap, catchError } from 'rxjs/operators';
import { UserDeviceInformationService } from './services/user-device-information.service';
import { AdminDeviceInformationService } from './services/admin-device-information.service';
import { Device, DeviceStatus, UpdateAction, ModifiedDevice } from './models/device-management-types';
import { NotificationService } from '@backbase/ui-ang';
import { DeviceInformationPreferenceService } from './services/device-information-preference.service';

@Component({
  selector: 'bb-device-information-widget',
  templateUrl: './device-information-widget.component.html',
  providers: [DeviceInformationPreferenceService, UserDeviceInformationService, AdminDeviceInformationService],
})
export class DeviceInformationWidgetComponent implements OnInit {
  @ViewChild('notificationSuccessHeader') successHeader: TemplateRef<any> | string = '';
  @ViewChild('notificationSuccessMessage') successMessage: TemplateRef<any> | string = '';
  @ViewChild('notificationFailureHeader') failureHeader: TemplateRef<any> | string = '';
  @ViewChild('notificationFailureMessage') failureMessage: TemplateRef<any> | string = '';
  @ViewChild('notificationPermissionsFailureMessage') failurePermissionsMessage: TemplateRef<any> | string = '';

  /**
   * Time to live for the success notification message in seconds
   */
  /* tslint:disable-next-line:no-input-rename */
  @Input('dismissTimeout') notificationDismissTime: number | undefined;

  /**
   * Set the user type of the widget to change it's behaviour
   */
  /* tslint:disable-next-line:no-input-rename */
  @Input('isAdminWidget') isAdmin: boolean | undefined;

  @Input()
  set dbsUserId(inputUserId: string) {
    this.adminDataService.dbsUserIdInput = inputUserId;
  }

  /**
   * Sets the id for the user to get device information for.
   */
  get dbsUserId() {
    return this.adminDataService.dbsUserIdInput;
  }

  updateActions = UpdateAction;
  updateAction: UpdateAction | undefined;
  private managedDevice: ModifiedDevice | undefined;
  private readonly refreshDevicesSubject = new BehaviorSubject<void>(undefined);
  private readonly isLoadingSubject = new BehaviorSubject<boolean>(false);
  private readonly isSavingSubject = new BehaviorSubject<boolean>(false);
  private readonly errorSubject = new BehaviorSubject<Error | undefined>(undefined);
  private dismissTimeout = 3000;
  private isAdminWidget = false;
  readonly isLoading = this.isLoadingSubject.asObservable();
  readonly isSaving = this.isSavingSubject.asObservable();
  readonly errorState = this.errorSubject.asObservable();
  readonly devices = this.getDevicesObservable();

  constructor(
    private readonly userDataService: UserDeviceInformationService,
    private readonly adminDataService: AdminDeviceInformationService,
    private readonly notificationService: NotificationService,
    private readonly preferenceService: DeviceInformationPreferenceService,
  ) {}

  ngOnInit(): void {
    if (this.notificationDismissTime) {
      this.dismissTimeout = this.notificationDismissTime * 1000;
    } else {
      this.preferenceService
        .getTimeoutPreference()
        .pipe(first())
        .subscribe(time => (this.dismissTimeout = time * 1000));
    }

    if (typeof this.isAdmin === 'boolean') {
      this.isAdminWidget = this.isAdmin;
    } else {
      this.preferenceService
        .getAdminPreference()
        .pipe(first())
        .subscribe(adminPreference => (this.isAdminWidget = adminPreference));
    }
  }

  getFriendlyName(defaultValue: string, modifiedDevice?: ModifiedDevice) {
    const device = modifiedDevice || this.managedDevice;
    return device && device.friendlyName ? `'${device.friendlyName}'` : defaultValue;
  }

  getOldFriendlyName(defaultValue: string, modifiedDevice?: ModifiedDevice) {
    const device = modifiedDevice || this.managedDevice;
    return device && device.oldFriendlyName ? `'${device.oldFriendlyName}'` : defaultValue;
  }

  onSave(device: ModifiedDevice) {
    this.isSavingSubject.next(true);
    this.updateAction = UpdateAction.Save;
    this.managedDevice = device;

    this.dataService
      .updateDevice(device.deviceId, {
        friendlyName: device.friendlyName,
      })
      .pipe(
        tap(() => this.isSavingSubject.next(false)),
        catchError((err: HttpErrorResponse) => {
          this.isSavingSubject.next(false);
          return this.onError(err);
        }),
      )
      .subscribe(this.onSuccess);
  }

  onRemove(device: Device) {
    this.updateAction = UpdateAction.Remove;
    this.managedDevice = device;
    this.dataService
      .removeDevice(device.deviceId)
      .pipe(catchError(this.onError))
      .subscribe(this.onSuccess);
  }

  onSuspend(device: Device) {
    this.updateAction = UpdateAction.Suspend;
    this.managedDevice = device;
    this.updateDevice(device, DeviceStatus.DISABLED);
  }

  onRestore(device: Device) {
    this.updateAction = UpdateAction.Restore;
    this.managedDevice = device;
    this.updateDevice(device, DeviceStatus.ENABLED);
  }

  private getDevicesObservable() {
    return this.refreshDevicesSubject.pipe(
      switchMap(() => {
        this.errorSubject.next(undefined);
        this.isLoadingSubject.next(true);
        return this.dataService.getDevices();
      }),
      tap(() => {
        this.isLoadingSubject.next(false);
      }),
      catchError(error => {
        this.isLoadingSubject.next(false);
        this.errorSubject.next(error);
        return new Observable();
      }),
    );
  }

  private updateDevice(device: Device, action: DeviceStatus) {
    this.dataService
      .updateDevice(device.deviceId, {
        status: action,
      })
      .pipe(catchError(this.onError))
      .subscribe(this.onSuccess);
  }

  private readonly onError = (err: HttpErrorResponse) => {
    this.notificationService.showNotification({
      header: this.failureHeader,
      message: this.getErrorMessage(err),
      messageContext: {
        device: this.managedDevice,
      },
      modifier: 'error',
      ttl: this.dismissTimeout,
    });
    return new Observable();
  };

  private readonly onSuccess = () => {
    this.notificationService.showNotification({
      header: this.successHeader,
      headerContext: {
        actionType: this.updateAction,
      },
      message: this.successMessage as TemplateRef<any>,
      messageContext: {
        actionType: this.updateAction,
        device: this.managedDevice,
      },
      modifier: 'success',
      ttl: this.dismissTimeout,
    });
    this.refreshDevicesSubject.next(undefined);
  };

  private getErrorMessage(error: HttpErrorResponse) {
    return (error.status === 403 ? this.failurePermissionsMessage : this.failureMessage) as TemplateRef<any>;
  }

  private get dataService(): AdminDeviceInformationService | UserDeviceInformationService {
    return this.isAdminWidget ? this.adminDataService : this.userDataService;
  }
}
