import { Injectable } from '@angular/core';
import { Device, ManageOtherUsersDevicesService, PatchDeviceRequest } from '@backbase/data-ang/device';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class AdminDeviceInformationService {
  private dbsUserId = '';

  constructor(private readonly deviceManagementService: ManageOtherUsersDevicesService) {}

  getDevices(): Observable<Array<Device>> {
    return this.deviceManagementService
      .getDevicesByDbsUserId({ dbsUserId: this.dbsUserId })
      .pipe(map(response => response.devices));
  }

  updateDevice(deviceId: string, patchDeviceRequest: PatchDeviceRequest): Observable<any> {
    return this.deviceManagementService.patchUserDeviceByDbsUserIdAndDeviceId({
      dbsUserId: this.dbsUserId,
      deviceId,
      patchDeviceRequest,
    });
  }

  removeDevice(deviceId: string): Observable<any> {
    return this.deviceManagementService.deleteUserDeviceByDeviceId({
      dbsUserId: this.dbsUserId,
      deviceId,
    });
  }

  public set dbsUserIdInput(userId: string) {
    this.dbsUserId = userId;
  }

  public get dbsUserIdInput() {
    return this.dbsUserId;
  }
}
